import { motion } from "framer-motion";
import { FiTwitter, FiLinkedin, FiFacebook, FiInstagram } from "react-icons/fi";
const Header = () => {
  return (
    <header className="absolute w-full z-50 transition-all duration-300">
      <div className="container mx-auto px-4 sm:px-6 lg:px-8 flex items-center justify-between h-16 md:h-20 ">
        {/* Logo/Name */}
        <motion.div
          initial={{ opacity: 0, x: -100 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{
            type: "spring",
            stiffness: 100,
            damping: 25,
            delay: 0.3,
            duration: 1.2,
          }}
          className="flex items-center"
        >
          {/* {my } */}
          <div className="h-10 w-10 rounded-3xl  bg-gradient-to-r from-yellow-500 to-yellow-700 flex items-center justify-center text-orange-700 font-bold text-xl  mr-3 ">
            M-Y
          </div>
          {/* {FullName} */}
          <span className="text-xl font-bold bg-gradient-to-r from-gray-300 to-gray-100 bg-clip-text text-transparent">
            FullName
          </span>
        </motion.div>
        {/* Desktop Navigation */}
        <nav className="lg:flex hidden space-x-8">
          {["Home", "About", "Project", "experience", "Contact"].map(
            (item, index) => (
              <motion.a
                key={item}
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{
                  type: "spring",
                  stiffness: 100,
                  damping: 20,
                  delay: 0.7 + index * 0.2,
                }}
                className="relative text-gray-800 dark:text-gray-200 
                hover:orange-300 dark:hover:text-orange-600 font-medium 
                transition-colors duration-300 group"
                href="#"
              >
                {item}
                <span className="absolute bottom-0 left-0 w-0 h-0.5 bg-yellow-500 group-hover:w-full transition-all duration-300"></span>
              </motion.a>
            )
          )}
          {/* { social icon } */}
          <div className="md:flex hidden items-center space-x-4">
            <motion.a
              initial={{ opacity: 0, scale: 0.5 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{
                delay: 1.5,
                duration: 0.8,
              }}
              className="text-gray-800 dark:text-gray-300 hover:text-orange-300 dark:hover:text-orange-600 transition-colors duration-300 "
              href="#"
            >
              <FiTwitter className="w-5 h-5" />
            </motion.a>
             <motion.a
              initial={{ opacity: 0, scale: 0.5 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{
                delay: 1.5,
                duration: 0.8,
              }}
              className="text-gray-800 dark:text-gray-300 hover:text-orange-300 dark:hover:text-orange-600 transition-colors duration-300 "
              href="#"
            >
              <FiInstagram className="w-5 h-5" />
            </motion.a>
            <motion.a
              initial={{ opacity: 0, scale: 0.5 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{
                delay: 1.5,
                duration: 0.8,
              }}
              className="text-gray-800 dark:text-gray-300 hover:text-orange-300 dark:hover:text-orange-600 transition-colors duration-300 "
              href="#"
            >
              <FiFacebook className="w-5 h-5" />
            </motion.a>
            <motion.a
              initial={{ opacity: 0, scale: 0.5 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{
                delay: 1.5,
                duration: 0.8,
              }}
              className="text-gray-800 dark:text-gray-300 hover:text-orange-300 dark:hover:text-orange-600 transition-colors duration-300 "
              href="#"
            >
              <FiLinkedin className="w-5 h-5" />
            </motion.a>

          </div>
        </nav>
      </div>
    </header>
  );
};

export default Header;
